import { Component, OnInit, ViewChild, HostListener } from '@angular/core';
import { Router } from '@angular/router';
import { WorkPermitService } from 'src/app/api/controllers/WorkPermit';
import { CookieService } from 'ngx-cookie-service';
import { jqxGridComponent } from 'src/app/shared/jqwidgets-scripts/jqwidgets-ts/angular_jqxgrid';
import * as moment from 'moment';

@Component({
  selector: 'app-workpermit-approved-list',
  templateUrl: './workpermit-approved-list.component.html',
  styleUrls: ['./workpermit-approved-list.component.scss']
})
export class WorkpermitApprovedListComponent implements OnInit {
  @ViewChild('datagrid', { static: false }) datagrid: jqxGridComponent;
  gridSourceData:any;
  columnData:any;
  wpData:string = "";
  workPermitListData:any;
  isDataLoaded:boolean = false;
  totalItems:any

  constructor(
    private router: Router,
    private cookieService:CookieService,
    public workPermit: WorkPermitService,
  ) { }

  onGlSearchFilter() {
    if(this.wpData != ""){
      let filtergroup = new jqx.filter();
      let filter_or_operator = 1;
      let filtervalue = this.wpData;
      let filtercondition = 'contains';
      let filterData = filtergroup.createfilter('stringfilter', filtervalue, filtercondition);
      filtergroup.operator = 'or';
      filtergroup.addfilter(filter_or_operator, filterData);
      this.datagrid.showfiltercolumnbackground(false);
      this.columnData.forEach(item => {
        if(item.datafield != 'Actions') {
          this.datagrid.addfilter(item.datafield, filtergroup, true);
        }
      })
      this.datagrid.applyfilters();
    }
    else {
      this.datagrid.clearfilters();
    }
  }

  @HostListener('window:onviewPermit', ['$event.detail'])
  onViewTicket(detail) {
    let dataRecord = this.datagrid.getrowdata(detail.rowId);
    let permitId = dataRecord.workPermitId
    this.router.navigate(['/ams/work-permit/create'],{queryParams:{id:permitId,type:'view'}});
  }

  ngOnInit() {
    let params = {
      apartmentId: parseInt(this.cookieService.get('apartmentId')),
      statusIds: '355'
    }
    this.workPermit.getAllWorkPermitsByStatues(params).subscribe((res:any) => {
      this.workPermitListData = res;
      this.totalItems = this.workPermitListData.length;
      this.gridSourceData = {
        localdata: this.workPermitListData,
        datatype: "array"
      }
      this.workPermitListData = new jqx.dataAdapter(this.gridSourceData);
      this.isDataLoaded = true;
    });

    var cellsrenderer = (row, column, value) => {
      return '<div class="jqx-custom-inner-cell">' + value + '</div>';
    }
    var columnrenderer = (value) => {
      return '<div style="padding: 14px">' + value + '</div>';
    }
    this.columnData = [{
        text: 'WP ID',
        datafield: 'workPermitId',
        width: 80,
        pinned: true,
        cellsrenderer: cellsrenderer,
        renderer: columnrenderer
    },{
        text: 'BLOCK',
        datafield: 'apartmentBlockNumber',
        minwidth: 130,
        cellsrenderer: cellsrenderer,
        renderer: columnrenderer,
    },{
        text: 'UNIT',
        datafield: 'apartmentBlockUnitNumber',
        cellsrenderer: cellsrenderer,
        minwidth: 80,
        renderer: columnrenderer
    },{
        text: 'ENDORSED BY',
        datafield: 'endorsedBy',
        cellsrenderer: cellsrenderer,
        minwidth: 140,
        renderer: columnrenderer
    },{
        text: 'WP TYPE',
        datafield: 'workPermitType',
        cellsrenderer: cellsrenderer,
        minwidth: 150,
        renderer: columnrenderer
    },{
        text: 'NATURE OF WORK',
        datafield: 'natureOfWork',
        cellsrenderer: cellsrenderer,
        minwidth: 150,
        renderer: columnrenderer
    },{
        text: '	START DATE',
        datafield: 'startDate',
        cellsrenderer: (row, column, value) => {
          return '<div class="jqx-custom-inner-cell">'+ moment(value).format("DD-MM-YYYY")+'</div>'
        },
        minwidth: 80,
        renderer: columnrenderer
    },{
      text: 'END DATE',
      datafield: 'endDate',
      cellsrenderer: (row, column, value) => {
        return '<div class="jqx-custom-inner-cell">'+ moment(value).format("DD-MM-YYYY")+'</div>'
      },
      minwidth: 80,
      renderer: columnrenderer
    },{
      text: 'PERSONNELS',
      datafield: 'noOfPersonnels',
      cellsalign: 'center', 
      align: 'center',
      cellsrenderer: cellsrenderer,
      minwidth: 80,
      renderer: columnrenderer,
    },{
      text: 'Actions',
      cellsalign: 'center', 
      align: 'center',
      width: 120,
      cellsrenderer: (row: number): string => {
        return '<div class="simple-actions">'
        +'<a href="javascript:void(0)" class="mr-3" onClick="viewPermitEvent('+ row +')"><i class="fa fa-eye icon view" aria-hidden="true"></i></a>'
      },
      renderer: columnrenderer
    }];
  }
}

function viewPermitEvent(row){
  var event = new CustomEvent('onviewPermit', {
    detail: {
      rowId: row
    }
  })
  window.dispatchEvent(event);
}
(window as any).viewPermitEvent = viewPermitEvent;
