import { Component, OnInit, ViewChild, HostListener } from '@angular/core';
import { Router, ActivatedRoute } from '@angular/router';
import { WorkPermitService } from 'src/app/api/controllers/WorkPermit';
import { CookieService } from 'ngx-cookie-service';
import { jqxGridComponent } from 'src/app/shared/jqwidgets-scripts/jqwidgets-ts/angular_jqxgrid';
import * as _ from 'underscore';
import * as moment from 'moment';
import { MatDialog } from '@angular/material/dialog';
import { WorkpermitStatusComponent } from '../workpermit-status/workpermit-status.component';

@Component({
  selector: 'app-workpermit-approval',
  templateUrl: './workpermit-approval.component.html',
  styleUrls: ['./workpermit-approval.component.scss']
})
export class WorkpermitApprovalComponent implements OnInit {
  @ViewChild('datagrid', { static: false }) datagrid: jqxGridComponent;
  gridSourceData:any;
  columnData:any;
  wpData:string = "";
  workPermitListData:any;
  isDataLoaded:boolean = false;
  totalItems:any

  constructor(
    private router: Router,
    private route: ActivatedRoute,
    private cookieService:CookieService,
    public workPermit: WorkPermitService,
    public dialog: MatDialog
  ) { }

  getWorkApprovalList() {
    let params = {
      apartmentId: parseInt(this.cookieService.get('apartmentId')),
      statusIds: '353,354,356,357,358,359'
    }
    this.workPermit.getAllWorkPermitsByStatues(params).subscribe((res:any) => {
      this.workPermitListData = res;
      this.totalItems = this.workPermitListData.length;
      this.gridSourceData = {
        localdata: this.workPermitListData,
        datatype: "array"
      }
      this.workPermitListData = new jqx.dataAdapter(this.gridSourceData);
      this.isDataLoaded = true;
    });
  }
  
  onGlSearchFilter() {
    if(this.wpData != ""){
      let filtergroup = new jqx.filter();
      let filter_or_operator = 1;
      let filtervalue = this.wpData;
      let filtercondition = 'contains';
      let filterData = filtergroup.createfilter('stringfilter', filtervalue, filtercondition);
      filtergroup.operator = 'or';
      filtergroup.addfilter(filter_or_operator, filterData);
      this.datagrid.showfiltercolumnbackground(false);
      this.columnData.forEach(item => {
        if(item.datafield != 'Actions') {
          this.datagrid.addfilter(item.datafield, filtergroup, true);
        }
      })
      this.datagrid.applyfilters();
    }
    else {
      this.datagrid.clearfilters();
    }
  }


  @HostListener('window:oneditPermit', ['$event.detail'])
  onEditTicket(detail) {
    let dataRecord = this.datagrid.getrowdata(detail.rowId);
    let permitId = dataRecord.workPermitId
    this.router.navigate(['/ams/work-permit/create'],{queryParams:{id:permitId,type:'edit'}});
  }

  @HostListener('window:onviewPermit', ['$event.detail'])
  onViewTicket(detail) {
    let dataRecord = this.datagrid.getrowdata(detail.rowId);
    let permitId = dataRecord.workPermitId
    this.router.navigate(['/ams/work-permit/create'],{queryParams:{id:permitId,type:'view'}});
  }

  @HostListener('window:onstatusPermit', ['$event.detail'])
  onstatusPermit(detail) {
    let dataRecord = this.datagrid.getrowdata(detail.rowId);
    let entity = {
      workpermitId : dataRecord.workPermitId,
      statusId : dataRecord.statusId
    }
    const dialogRef = this.dialog.open(WorkpermitStatusComponent, {
      width: '600px',
      height:'350px',
      data:entity
    });
    dialogRef.afterClosed().subscribe(result => {
      if(result) {
        this.getWorkApprovalList();
      }
    });
  }


  ngOnInit() {
    this.getWorkApprovalList();
    var cellsrenderer = (row, column, value) => {
      return '<div class="jqx-custom-inner-cell">' + value + '</div>';
    }
    var columnrenderer = (value) => {
      return '<div style="padding: 14px">' + value + '</div>';
    }
    this.columnData = [{
        text: 'WP ID',
        datafield: 'workPermitId',
        width: 80,
        pinned: true,
        cellsrenderer: cellsrenderer,
        renderer: columnrenderer
    },{
        text: 'BLOCK',
        datafield: 'apartmentBlockNumber',
        minwidth: 130,
        cellsrenderer: cellsrenderer,
        renderer: columnrenderer,
    },{
        text: 'UNIT',
        datafield: 'apartmentBlockUnitNumber',
        cellsrenderer: cellsrenderer,
        minwidth: 80,
        renderer: columnrenderer
    },{
        text: 'ENDORSED BY',
        datafield: 'endorsedBy',
        cellsrenderer: cellsrenderer,
        minwidth: 140,
        renderer: columnrenderer
    },{
        text: 'WP TYPE',
        datafield: 'workPermitType',
        cellsrenderer: cellsrenderer,
        minwidth: 150,
        renderer: columnrenderer
    },{
        text: 'NATURE OF WORK',
        datafield: 'natureOfWork',
        cellsrenderer: cellsrenderer,
        minwidth: 150,
        renderer: columnrenderer
    },{
        text: '	START DATE',
        datafield: 'startDate',
        cellsrenderer: (row, column, value) => {
          return '<div class="jqx-custom-inner-cell">'+ moment(value).format("DD-MM-YYYY")+'</div>'
        },
        minwidth: 80,
        renderer: columnrenderer
    },{
      text: 'END DATE',
      datafield: 'endDate',
      cellsrenderer: (row, column, value) => {
        return '<div class="jqx-custom-inner-cell">'+ moment(value).format("DD-MM-YYYY")+'</div>'
      },
      minwidth: 80,
      renderer: columnrenderer
    },{
      text: 'PERSONNELS',
      datafield: 'noOfPersonnels',
      cellsalign: 'center', 
      align: 'center',
      cellsrenderer: cellsrenderer,
      minwidth: 80,
      renderer: columnrenderer,
    },{
      text: 'Status',
      datafield: 'status',
      cellsalign: 'center', 
      align: 'center',
      cellsrenderer: cellsrenderer,
      minwidth: 160,
      renderer: columnrenderer,
    },{
        text: 'Actions',
        cellsalign: 'center', 
        align: 'center',
        width: 120,
        cellsrenderer: (row: number): string => {
          return '<div class="simple-actions">'
          +'<a href="javascript:void(0)" class="mr-3" onClick="viewPermitEvent('+ row +')"><i class="fa fa-eye icon view" aria-hidden="true"></i></a>'
          +'<a href="javascript:void(0)" class="mr-3" onClick="editPermitEvent('+ row +')"><i class="fa fa-pencil icon edit" aria-hidden="true"></i></a>'
          +'<a href="javascript:void(0)" onClick="statusPermitEvent('+ row +')"><i class="fa fa-check-circle icon delete" aria-hidden="true"></i></a></div>'
        },
        renderer: columnrenderer
    }];
  }

}

function statusPermitEvent(row){
  var event = new CustomEvent('onstatusPermit', {
    detail: {
        rowId: row
    }
  })
  window.dispatchEvent(event);
}
(window as any).statusPermitEvent = statusPermitEvent;


function editPermitEvent(row){
  var event = new CustomEvent('oneditPermit', {
    detail: {
      rowId: row
    }
  })
  window.dispatchEvent(event);
}
(window as any).editPermitEvent = editPermitEvent;

function viewPermitEvent(row){
  var event = new CustomEvent('onviewPermit', {
    detail: {
      rowId: row
    }
  })
  window.dispatchEvent(event);
}
(window as any).viewPermitEvent = viewPermitEvent;
