import { Component, OnInit } from '@angular/core';
import { Router, NavigationStart, NavigationEnd, ActivatedRoute } from '@angular/router';

import { LookupService } from 'src/app/api/controllers/Lookup';
import { SharedService } from '../../../../shared/services/shared.service';
import { CookieService } from 'ngx-cookie-service';

import * as _ from 'underscore';

@Component({
  selector: 'app-workpermit-setup',
  templateUrl: './workpermit-setup.component.html',
  styleUrls: ['./workpermit-setup.component.scss']
})
export class WorkpermitSetupComponent implements OnInit {
  selectedTab:any;
  workpermitEditmode:boolean = false;
  natureEditMode:boolean = false;
  workpermit:any;
  naturework:any;

  isDataLoaded:boolean = false;

  wpData:string = "";
  nwData:string = "";

  unitFieldType:string = "unitno";
  unitOrder:boolean = true;

  ItemStartIndex:any = 0;
  ItemEndIndex:any;
  totalItems:any;
  itemLimit:any = 10;
  totalFilterItems:any;

  //Nature Pagination
  NWunitFieldType:string = "unitno";
  NWunitOrder:boolean = true;

  NWItemStartIndex:any = 0;
  NWItemEndIndex:any;
  NWtotalItems:any;
  NWitemLimit:any = 10;
  NWtotalFilterItems:any;

  //Get all work type
  allWorkType:any;
  allNatureWork:any;

  constructor(
    private router: Router,
    private route: ActivatedRoute,
  	private lookupService: LookupService,
    private sharedService:SharedService,
    private cookieService:CookieService
  ) { }

    // Tab
  showTab(type){
  	this.selectedTab = type;
  }
    // Tab
  isActive(type) {
    return this.selectedTab == type ? true : false;
  }

  getIndexParams(event){
    this.ItemStartIndex = event.ItemStartIndex;
    this.ItemEndIndex = event.ItemEndIndex;
  }

  sortUnitData(type){
    this.unitFieldType = type;
    this.unitOrder = !this.unitOrder;
  }

  getFieldOrderBy(type) {
    if(this.unitFieldType == type){
      return this.unitOrder ? 'asc' : 'desc';
    }
    else return '';
  }

  isMobileView() {
    return window.innerWidth <= 767 ? 'table-responsive' : '';
  }

  //Get Work Type
  getWorktype(){
    let params = {
      LookupTypeId: 90
    }
    this.lookupService.getLookupValueByLookupTypeId(params).subscribe((res:any) => {
      this.allWorkType = res;
      this.isDataLoaded = true;
      this.totalItems = this.allWorkType.length;
      if(this.totalItems>this.itemLimit){
        this.ItemEndIndex = this.itemLimit;
      }
      else {
        this.ItemEndIndex = this.totalItems;
      }
    })
  }

  //Get Work Type
  getNaturework(){
    let params = {
      LookupTypeId: 89
    }
    this.lookupService.getLookupValueByLookupTypeId(params).subscribe((res:any) => {
      this.allNatureWork = res;
      this.isDataLoaded = true;
      this.NWtotalItems = this.allNatureWork.length;
      if(this.NWtotalItems>this.NWitemLimit){
        this.NWItemEndIndex = this.NWitemLimit;
      }
      else {
        this.NWItemEndIndex = this.NWtotalItems;
      }
    })
  }

  editWork(data,type) {
    if(type == 'work') {
      this.workpermitEditmode = true;
      this.workpermit.WPtype = data.lookupValueName;
      this.workpermit.WPdescription = data.description;
      this.workpermit.lookupValueId = data.lookupValueId;
    } else {
      this.natureEditMode = true;
      this.naturework.WPtype = data.lookupValueName;
      this.naturework.WPdescription = data.description;
      this.naturework.lookupValueId = data.lookupValueId;
    }
  }

  cancelEditMode(type) {
    if(type == 'work') {
      this.workpermitEditmode = false;
      this.workpermit.WPtype = '';
      this.workpermit.WPdescription = '';
      this.workpermit.lookupValueId = '';
    } else {
      this.natureEditMode = false;
      this.naturework.WPtype = '';
      this.naturework.WPdescription = '';
      this.naturework.lookupValueId = '';
    }
  }

  submitCreateWorkPermitTypeForm(){
    let workpermittypes={
      "lookupValueId": 0,
      "apartmentId": parseInt(this.cookieService.get('apartmentId')),
      "lookupTypeId": 90,
      "lookupValueName": this.workpermit.WPtype,
      "description": this.workpermit.WPdescription,
      "isActive": true,
      "insertedBy": parseInt(this.cookieService.get('userId')),
      "insertedOn": new Date().toISOString(),
      "updatedBy": null,
      "updatedOn": null
    }
    let params = {
      lookupvalue: workpermittypes
    }
    this.lookupService.addLookupValue(params).subscribe((res:any) => {
      if(res.message) {
        this.sharedService.setAlertMessage("WorkPermit Type added successfully");
        this.getWorktype();
      }
    });
  }

  submitCreateNatureofWorkForm(){
    let workpermittypes={
      "lookupValueId": 0,
      "apartmentId": parseInt(this.cookieService.get('apartmentId')),
      "lookupTypeId": 89,
      "lookupValueName": this.naturework.WPtype,
      "description": this.naturework.WPdescription,
      "isActive": true,
      "insertedBy": parseInt(this.cookieService.get('userId')),
      "insertedOn": new Date().toISOString(),
      "updatedBy": null,
      "updatedOn": null
    }
    let params = {
      lookupvalue: workpermittypes
    }
    this.lookupService.addLookupValue(params).subscribe((res:any) => {
      if(res.message) {
        this.sharedService.setAlertMessage("WorkPermit Nature of work added successfully");
        this.getNaturework();
      }
    });
  }

  updateCreateWorkPermitTypeForm(){
    let workpermittypes={
      "lookupValueId": this.workpermit.lookupValueId,
      "apartmentId": parseInt(this.cookieService.get('apartmentId')),
      "lookupTypeId": 90,
      "lookupValueName": this.workpermit.WPtype,
      "description": this.workpermit.WPdescription,
      "isActive": true,
      "insertedBy": parseInt(this.cookieService.get('userId')),
      "insertedOn": new Date().toISOString(),
      "updatedBy": null,
      "updatedOn": null
    }
    let params = {
      lookupvalue: workpermittypes
    }
    this.lookupService.updateLookupValue(params).subscribe((res:any) => {
      if(res.message) {
        this.workpermitEditmode = false;
        this.sharedService.setAlertMessage("WorkPermit Type added successfully");
        this.getWorktype();
      }
    });
  }

  updateCreateNatureofWorkForm(){
    let workpermittypes={
      "lookupValueId": this.naturework.lookupValueId,
      "apartmentId": parseInt(this.cookieService.get('apartmentId')),
      "lookupTypeId": 89,
      "lookupValueName": this.naturework.WPtype,
      "description": this.naturework.WPdescription,
      "isActive": true,
      "insertedBy": parseInt(this.cookieService.get('userId')),
      "insertedOn": new Date().toISOString(),
      "updatedBy": null,
      "updatedOn": null
    }
    let params = {
      lookupvalue: workpermittypes
    }
    this.lookupService.updateLookupValue(params).subscribe((res:any) => {
      if(res.message) {
        this.natureEditMode = false
        this.sharedService.setAlertMessage("WorkPermit Nature of work added successfully");
        this.getNaturework();
      }
    });
  }

  deleteWork(data,type) {
    let params = {
      lookupValueId : data.lookupValueId,
      updateUserId :  parseInt(this.cookieService.get('userId'))
    }
    this.lookupService.deleteLookupvalue(params).subscribe((res:any) => {
      if(res) {
        if(type == 'work') {
          this.getWorktype();
        } else {
          this.getNaturework();
        }
      }
    });
  }

  ngOnInit(): void {
    this.workpermit = {};
    this.naturework = {};
    this.getWorktype();
    this.getNaturework();
    this.selectedTab = 'WorkPermit';  //Default selected tab
  }
}
