import { Component, OnInit, Inject } from '@angular/core';
import { MAT_DIALOG_DATA, MatDialogRef } from '@angular/material/dialog';
import { LookupService } from 'src/app/api/controllers/Lookup';
import { WorkPermitService } from 'src/app/api/controllers/WorkPermit';
import { CookieService } from 'ngx-cookie-service';

@Component({
  selector: 'app-workpermit-status',
  templateUrl: './workpermit-status.component.html',
  styleUrls: ['./workpermit-status.component.scss']
})
export class WorkpermitStatusComponent implements OnInit {
  status:any;
  statusTypeData:any;
  constructor(public dialogRef: MatDialogRef<WorkpermitStatusComponent>,
    @Inject(MAT_DIALOG_DATA) public data: any,
    private lookupService: LookupService,
    private workPermitService:WorkPermitService,
    private cookieService:CookieService,
  ) { }

  onConfirm(): void {
    let params = {
      workPermit: {
        apartmentId : parseInt(this.cookieService.get('apartmentId')),
        workPermitStatusId : this.status,
        workPermitId: this.data.workpermitId,
        insertedBy : parseInt(this.cookieService.get('userId')),
        insertedOn : new Date().toISOString(),
        isApproved : true,
        approvedBy: parseInt(this.cookieService.get('userId')),
        approvedOn: new Date().toISOString(),
      }
    }
    this.workPermitService.updateWorkPermitStatusById(params).subscribe((data)=>{
      this.dialogRef.close(true);
    })
  }

  onDismiss(): void {
    // Close the dialog, return false
    this.dialogRef.close(false);
  }

  ngOnInit(): void {
    let statusParams = {
      LookupTypeId: 91
    }
    this.lookupService.getLookupValueByLookupTypeId(statusParams).subscribe((res:any) => {
      this.statusTypeData = res;
      this.status = this.data.statusId;
    })
  }

}
